.contactsContainer{max-width:1200px;margin:0 auto;padding:30px 20px}.title{font-size:28px;font-weight:600;margin-bottom:30px;color:#333;text-align:center}.tabsContainer{display:flex;flex-wrap:wrap;gap:10px;margin-bottom:25px;justify-content:center}.tabButton{padding:8px 16px;background-color:#f5f5f5;border:none;border-radius:20px;font-size:14px;color:#555;cursor:pointer;transition:all .2s ease}.tabButton:hover{background-color:#e0e0e0}.activeTab{background-color:#2196f3;color:#fff}.activeTab:hover{background-color:#1976d2}.tableContainer{background:#fff;border-radius:12px;box-shadow:0 2px 15px rgba(0,0,0,.08);overflow:hidden;overflow-x:auto}.contactsTable{width:100%;border-collapse:collapse}.contactsTable th{text-align:left;padding:16px;background:#f8f9fa;font-weight:600;color:#333;border-bottom:2px solid #e0e0e0;white-space:nowrap}.contactsTable td{padding:16px;border-bottom:1px solid #eee;vertical-align:middle}.sortableHeader{cursor:pointer;user-select:none;transition:background-color .2s}.sortableHeader:hover{background-color:#e9ecef}.sortIcon{margin-left:5px;font-size:12px;color:#2196f3}.contactRow{transition:background-color .2s}.contactRow:hover{background-color:#f8f9fa}.phoneLink,.emailLink{color:#2196f3;text-decoration:none;transition:color .2s}.phoneLink:hover,.emailLink:hover{color:#1976d2;text-decoration:underline}.noContacts{text-align:center;padding:40px;color:#666;font-style:italic;background:#fff;border-radius:12px;box-shadow:0 2px 15px rgba(0,0,0,.08);margin-top:20px}.showMoreContainer{margin-top:20px;padding:0 20px;width:100%}.showMoreButton{width:100%;padding:12px 20px;background-color:#2196f3;color:#fff;border:none;border-radius:6px;font-size:16px;cursor:pointer;transition:background-color .2s;text-align:center}.showMoreButton:hover{background-color:#1976d2}@media(max-width: 768px){.contactsTable th,.contactsTable td{padding:12px 8px;font-size:14px}}@media(max-width: 576px){.tabsContainer{justify-content:flex-start;overflow-x:auto;padding-bottom:10px}.contactsTable th:nth-child(2),.contactsTable td:nth-child(2),.contactsTable th:nth-child(4),.contactsTable td:nth-child(4){display:none}}